@import url(//fonts.googleapis.com/css?family=Open+Sans);

body {
  font-family: 'Open Sans';
  color: #343A40;
}


.white-rounded-shadowed {
  background-color: #fff;
  box-shadow: #d1d1d1 0 4px 3px 1px;
  border-radius: 5px;
  padding: 3rem;
}


.align-to-end {
  text-align: end;
}

.align-to-center {
  text-align: center;
}

.default-color {
  color: #343A40;
}

.custom-button-form {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  text-align: center;
  text-decoration: none;
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 8px 16px;
  width: 185px;
  height: 50px;
  left: 198px;
  top: 427px;
  margin-top: 2rem;
  background: #45A2F8;
  border-radius: 4.8px;
  border: 1px solid #45A2F8;

}

.rec-div {
  margin-top: 3rem;
  display: inline-block;
}

.rec-div-invalid {
  margin-top: 3rem;
  display: inline-block;
  border: red 1px solid;
}


.text-xs-end {
  text-align: end;
}


.custom-button-form:hover {
  color: #45A2F8;
  background-color: #efefef;
}

.custom-button-richiesta {
  font-size: large;
  margin-bottom: 2rem;
  border: 1px solid #45A2F8;
  color: white;
  border-radius: 5px;
  background: #45A2F8;
  margin-top: 2rem;
}

.custom-button-richiesta:hover {
  color: #45A2F8;
  background-color: #efefef;
}


.text-red {
  color: #90343C;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.info-div {
  padding-top: 3rem;
  padding-bottom: 3rem;
  padding-right: 10rem;
  padding-left: 10rem;
}

hr {
  margin-top: 1rem;
  border-top: 1px solid #D4D9DE;
}

p {
  color: #343A40;
  text-transform: capitalize;
  font-weight: 400;
  font-size: large;

}

.p-yellow {
  font-weight: 500;
}

label {
  font-family: "Open Sans";
  font-weight: 400;
}

.custom-select {
  width: 30%;
  display: inline;
  margin-left: 1rem;
  background-color: #eee;
  color: #45A2F8;
  border: none;
}

.image-div-content {

  position: absolute;
  width: 522px;
  height: 280px;
  left: 175px;
  top: 249px;

  background: #FFFFFF;
}

.form-div {
  padding-left: 10rem;
  padding-right: 10rem;
  margin-top: 1rem;

}


.p-image {
  font-size: larger;
  font-family: "Open Sans";
  text-transform: none;
}

.image-div {
  height: 45rem;
  background-image: url("./img/businesswoman-typing-2022-03-30-20-23-35-utc 1.png");
  background-color: #3E8ACC;
  background-size: cover;
}

.back-col {
  background-color: #E5E5E5;
}

.from-text-header {
  color: #7F8297;
  font-size: small;
  font-weight: bold;
}

.form-text-header-required {
  color: #7F8297;
  font-size: smaller;
  font-weight: bold;
}


